import logo from './logo.svg';
import './App.css';
import {useEffect, useState} from "react";
import {isFocusable} from "@testing-library/user-event/dist/utils";

function App() {

  let post = '심천 이사 준비 중...';
  let [글제목, 글제목변경] = useState(['니에하우스 이사 준비', '니에하우스 가구 추천', '니에하우스 주변 맛집']);
  let [좋아요, 좋아요변경] = useState(new Array(글제목.length).fill(0));
  let [modal, setModal] = useState(false);
  let [title, setTitle] = useState(0);
  let [입력값, 입력값변경] = useState('');
  // const time = post[1] ? post[1].replace(")", "") : "";
  // useEffect(() => {
  //   좋아요변경(new Array(글제목.length).fill(0));}, [글제목]);
  return (
      <div className="App">
        <div className='black-nav'>
          <h4>니모와 에디의 심천라이프</h4>
        </div>
        {
          글제목.map(function (a, i) {
            return (
                <div className='list' key={i}>
                  <h4 onClick={() => {
                    setModal(!modal);
                    setTitle(i)
                  }
                  }> {글제목[i]}
                    <span onClick={(e) => {
                      e.stopPropagation()
                      let copy = [...좋아요];
                      copy[i] += 1;
                      좋아요변경(copy)
                    }
                    }> 🐻</span> {좋아요[i]}
                  </h4>
                  <p>3월 12일부터 시작!</p>
                  {/*{time && <p>{time}</p>}*/}
                  <button onClick={() => {
                    let copy1 = [...글제목];
                    let copy2 = [...좋아요];
                    copy1.splice(i, 1);
                    copy2.splice(i, 1);
                    글제목변경(copy1);
                    좋아요변경(copy2);
                  }}>삭제
                  </button>
                </div>)
          })
        }

        {
          modal == true ? <Modal title={title} 글제목={글제목} 글제목변경={글제목변경}/> : null
        }

        <hr/>
        <button onClick={() => {
          let copy = [...글제목];
          copy[0] = '니에하우스 이사 완료~!';
          글제목변경(copy);
        }}>수정
        </button>

        <button onClick={() => {
          let copy = [...글제목];
          copy.sort();
          글제목변경(copy);
        }}>정렬
        </button>
        <hr/>

        <input onChange={(e) => {
          입력값변경(e.target.value)
        }}/>
        {/* e.target.value 는 유저가 입력한 값*/}

        <button onClick={() => {
          if (!입력값) {
            alert("글을 입력해주세요!");
            return;
          }
          let copy = [...글제목];
          copy.unshift(입력값 + "(" + new Date().toLocaleTimeString() + ")");
          글제목변경(copy);

          //글 추가 시 좋아요 상태 배열도 함께 추가
          let likeCopy = [...좋아요];
          likeCopy.unshift(0);
          좋아요변경(likeCopy)
        }}>글발행
        </button>
        {/*// copy 맨 처음에 유저가 입력한 글 추가*/}

        <h4>{post}</h4>
      </div>
  );
}

function Modal(props) {
  return (
      <div className="modal">
        <h4>{props.글제목[props.title]}</h4>
        <p>날짜</p>
        <p>상세 내용</p>

        <button onClick={() => {
          let copy = [...props.글제목];
          copy[0] = '니에하우스 이사 완료~!';
          props.글제목변경(copy);
        }}>글수정
        </button>

      </div>
  )
}

export default App;
